// React
import { Component } from "react";

class CheckboxGroup extends Component {
  static defaultProps = {
    defaultStatus: true,
  };

  constructor(props) {
    super(props);
    this.state = {};
    this.handleItems = this.handleItems.bind(this);
    this.handleCheckboxStatus = this.handleCheckboxStatus.bind(this);
    this.handleCheckAllCheckboxes = this.handleCheckAllCheckboxes.bind(this);
  }

  componentDidMount() {
    this.handleItems();
  }

  handleCheckboxStatus(item) {
    const itemChanged = { [item]: !this.state[item] }

    const checkboxStatus = { ...this.state, ...itemChanged };
    delete checkboxStatus.all;

    const allCheckboxesAreChecked = Object.values(checkboxStatus).some((allChecked) => allChecked);

    if (allCheckboxesAreChecked) {
      this.setState({ ...itemChanged, all: true });
    } else {
      this.setState({ ...itemChanged, all: false });
    }
  }

  handleCheckAllCheckboxes() {
    const items = {};

    Object.keys(this.state).forEach((item) => {
      items[item] = !this.state.all;
    });

    this.setState({ ...items, all: !this.state.all });
  }

  handleItems() {
    const items = {};
    this.props.items.forEach((item) => {
      items[item._id] = this.props.defaultStatus;
    });
    this.setState({ ...items, all: this.props.defaultStatus });
  }

  render() {
    const items = this.props.renderItems({
      state: this.state,
      onChecked: this.handleCheckboxStatus,
      onAllChecked: this.handleCheckAllCheckboxes,
    });
    return items;
  }
}

export default CheckboxGroup;
