// React
import { Component } from "react";

// Librarys
import { Collapse } from "antd";

// JS
import { isArray } from "@assets/js/typeof";

/**
 * Crear un panel
 */

const { Panel } = Collapse;

class CustomCollapse extends Component {
  render() {
    const { children } = this.props;
    const childrenAreArray = isArray(children);
    const items = childrenAreArray && children?.map((item, i) => (
      <Panel header={item.props.children[i]} key={i}>{item}</Panel>
    ));
    return (
      <Collapse>
        {childrenAreArray ? items : (
          <Panel header={children} showArrow={false} >{children.props.items}</Panel>
        )}
      </Collapse>
    );
  }
}

export default CustomCollapse;
