// React
import { Component, Fragment, PureComponent } from "react";

// Components
import { CheckboxGroup } from "@elements";

// Librarys

class Categories extends Component {
  static defaultProps = {
    checkboxStatus: false,
  };

  constructor(props) {
    super(props);
    this.handleRef = this.handleRef.bind(this);
    this.renderCategories = this.renderCategories.bind(this);
    this.handleFilterByCategory = this.handleFilterByCategory.bind(this);
  }

  handleRef(ref) {
    if(ref) {
      this.status = ref
    }
  } 

  renderCategories({ state, onChecked, onAllChecked }) {
    return (
      <Fragment>
        <Category
          name="Todos"
          count={10}
          checked={state.all}
          onChecked={onAllChecked}
        />
        {this.props.items.map((item) => {
          function handleChecked() {
            onChecked(item._id);
          }
          return (
            <Category
              {...item}
              key={item._id}
              checked={state[item._id]}
              onChecked={handleChecked}
            />
          );
        })}
      </Fragment>
    );
  }

  handleFilterByCategory() {
    console.log(this.status.state)
  }

  render() {
    return (
      <div id="categories-container">
        <h2 className="title">Categorías:</h2>
        <p className="text">
          Selecciona entre las distintas categorías de los productos.
        </p>
        <ul className="categories">
          <CheckboxGroup
            ref={this.handleRef}
            items={this.props.items}
            renderItems={this.renderCategories}
          />
        </ul>
        <button className="apply-filters pointer" onClick={this.handleFilterByCategory}>
          Filtrar por categoría
        </button>
      </div>
    );
  }
}

export default Categories;

class Category extends PureComponent {
  static defaultProps = {
    checked: true,
    count: 0,
  };

  render() {
    const categoryIsActive = this.props.checked
      ? "align-items-center pointer active"
      : "align-items-center pointer";
    return (
      <li className="category">
        <span className={categoryIsActive} onClick={this.props.onChecked}>
          &nbsp;&nbsp;{this.props.name}&nbsp;({this.props.count})
        </span>
      </li>
    );
  }
}
