// React
import { Component } from "react";

// Librarys
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

// Styles
import "@styles/productos/styles.product.css";
import "@styles/productos/styles.product.responsive.css";

class Product extends Component {
  shouldComponentUpdate() {
    return false;
  }

  render() {
    return (
      <div className="product">
        <Star />
        <h3 className="title">{this.props.name}</h3>
        <figure>
          <Link to="/">
            <img
              src="https://emprendimientoysalud.com/productos-omnilife/imagenes/aqtua.webp"
              alt="ego-plant"
              title="Ego plant"
              width={400}
              height={400}
            />
          </Link>
        </figure>
        <p className="text-product">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
          minim veniam, quis nostrud
        </p>
      </div>
    );
  }
}

export default Product;

const star = "star pointer ";
const rubberBand = "active animate__animated animate__jackInTheBox";
const jello = "animate__animated animate__wobble";

class Star extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inFavorites: false,
    };
    this.addtoFavorites = this.addtoFavorites.bind(this);
  }

  addtoFavorites() {
    this.setState({ inFavorites: !this.state.inFavorites });
  }

  render() {
    return (
      <span className={this.state.inFavorites ? star + rubberBand : star + jello}>
        <FontAwesomeIcon
          icon={this.state.inFavorites ? "star" : ["far", "star"]}
          title="Añadir a favoritos"
          size="lg"
          onClick={this.addtoFavorites}
        />
      </span>
    );
  }
}
