// React
import React, { Component } from 'react';

// Librarys
import { Menu, Dropdown } from "antd";

class CustomDropdown extends Component {
  static defaultProps = {
    trigger: ["hover"],
    placement: "bottomCenter",
    subItems: []
  }
  constructor(props){
    super(props);
    this.menu = (
      <Menu>
        {this.props.subItems?.map((item, i) => (
          <Menu.Item key={i}>{item?.el}</Menu.Item>
        ))}
      </Menu>
    );
  }
  render() {
    return (
      <Dropdown {...this.props} overlay={this.menu}>
        {this.props.children}
      </Dropdown>
    )
  }
}

export default CustomDropdown;
