// React
import { Component } from "react";

// Components
import Product from "./Product";
import Categories from "./filters/Filters.Categories";
// import Seeker from "./filters/Filters.Seeker";
// import OrderBy from "./filters/Filters.OrderBy";

// Styles
import "@styles/productos/styles.productsContainer.css";
import "@styles/productos/styles.productsContainer.responsive.css";

const defaultCategories = Array.from(Array(20)).map((_, i) => {
  const formatted = i < 10 ? "0" + (i + 1) : i;
  return {
    _id: `categoría-${i}`,
    name: `Categoría ${formatted}`
  }
});

class ProductsContainer extends Component {
  static defaultProps = {
    categories: defaultCategories
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <section id="products-container" category={this.props.category}>
        <div className="wrapper">
          <div className="filters">
            <Categories items={this.props.categories} />
            <div className="wrap">

            </div>
          </div>
          <div className="products">
            {this.props.products.map((item, i) => (
              <Product {...item} key={item.id || i} />
            ))}
          </div>
        </div>
      </section>
    );
  }
}

export default ProductsContainer;
